/* Contact Section Styles */
#contact {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

#contact h2 {
  margin-bottom: 20px;
  font-size: 24px;
  color: #333;
}

#contact p {
  font-size: 16px;
  color: #666;
}

#contact ul {
  list-style: none; /* Remove default list bullets */
  padding: 0;
  margin: 20px 0;
  display: flex; /* Align list items horizontally */
  flex-wrap: wrap; /* Wrap to next line on smaller screens */
  gap: 20px; /* Space out the items */
}

#contact ul li {
  display: flex; /* Align items inside li horizontally */
  align-items: center;
  margin: 0; /* Remove margin for better alignment */
}

#contact ul li a {
  text-decoration: none;
  color: #333;
  font-size: 18px;
  display: flex;
  align-items: center;
  transition: color 0.3s, text-shadow 0.3s; /* Smooth transition for color and glow effect */
}

#contact form {
  display: flex;
  flex-direction: column;
  max-width: 600px;
  margin: 20px 0;
}

#contact form label {
  margin-bottom: 5px;
  font-weight: bold;
  color: #333;
  font-size: 16px; /* Set font size to 14px */
  font-family: Calibri, sans-serif; /* Set font to Calibri, fallback to sans-serif */
}

#contact form input {
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 14px; /* Set font size to 14px */
  font-family: Calibri, sans-serif; /* Set font to Calibri, fallback to sans-serif */
}

#contact form button {
  padding: 10px 15px;
  background-color: #333;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

#contact form button:hover {
  background-color: #444;
}

